import * as React from 'react';
import { Chip, Stack, Box } from '@mui/material';

type PickOrNotList = [string, string[]][];
type Colors = string[];

interface MemberPicksOrNotPicksProps {
  pickOrNotList: PickOrNotList;
  colors: Colors;
}

const MemberPicksOrNotPicks: React.FC<MemberPicksOrNotPicksProps> = ({ pickOrNotList, colors }) => {
  const item = pickOrNotList[0];

  return (
    <Box textAlign="center" width="100%" mt={2} p={2}>
      <h3 style={{ marginTop: 5 }}>{item[0]}</h3>
      <Stack direction="row" spacing={1} flexWrap="wrap" justifyContent="center">
        {item[1].map((number, idx) => (
          <Chip key={idx} label={number} style={{ backgroundColor: colors[idx % colors.length], marginTop: 10 }} />
        ))}
      </Stack>
    </Box>
  );
};

export default MemberPicksOrNotPicks;
